import service from "@/utils/request";
export const GetLevelsAsync = () => service.request({
    url: '/api/syllabus/levels/',
    method: 'get'
});
export const GetCoursesByLevelAsync = (level) => service.request(({
    url: `/api/syllabus/level/${level}/courses/`,
    method: 'get'
}));
export const GetContentsBySyllabusId = (sylId) => service.request({
    url: `/api/syllabus/${sylId}/contents/`,
    method: 'get'
});
export const AdminGetUserSyllabusAsync = (urlParams) => service.request({
    url: '/api/admin/user-syllabus/',
    method: 'get',
    params: urlParams
});
export const AdminCreateUserSyllabus = (contentInfo) => service.request({
    url: '/api/admin/user-syllabus/',
    method: 'post',
    data: contentInfo
});
export const AdminValidateUserSyllabusObjectById = (id, contentInfo) => service.request({
    url: `/api/admin/user-syllabus/${id}/validate/`,
    method: 'put',
    data: contentInfo
});
export const AdminDeleteUserSyllabusObjectById = (id) => service.request({
    url: `/api/admin/user-syllabus/${id}/`,
    method: 'delete'
});
